.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6em;
}
.gpt3__navbar_links {
  display: flex;
  flex: 1;
  align-items: center;
}
.gpt3__navbar_links__logo {
  margin-right: 2rem;
}
.gpt3__navbar_links__logo img {
  width: 100px;
  height: 100px;
}
.gpt3__navbar-links__container {
  display: flex;
  flex-direction: row;
}
.gpt3__navbar-sign {
  display: flex;
  align-items: center;
}
.gpt3__navbar-links__container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu__container p {
  color: #fff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}
.gpt3__navbar-sign button,
.gpt3__navbar-menu__container button {
  background: #ff4820;
  padding: 1rem 2rem;
  color: #ffffff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  border-radius: 5px;
  border: 0;
  outline: none;
  cursor: pointer;
}
.gpt3__navbar-menu {
  margin-left: 1rem;
  display: block;
  position: relative;
}
.gpt3__navbar-menu svg {
  cursor: pointer;
}
.gpt3__navbar-menu__container {
  display: flex;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu__container p {
  margin: 1rem 0;
}
.gpt3__navbar-menu__container-links-signs {
  display: none;
}
@media screen and (max-width: 1050px) {
  .gpt3__navbar-links__container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
}
@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign{
    display: none;
  }
  .gpt3__navbar-menu__container{
    top: 20px;
  }
  .gpt3__navbar-menu__container-links-signs{
    display: block;
  }
}
